// import 'antd/dist/antd.min.css';
import { Select } from 'antd';
import './App.css';
import { Button, Space, Popover } from 'antd';
import React, { useRef, useState } from 'react';
import { CloseOutlined } from '@ant-design/icons';
import { pinyin } from 'pinyin-pro';
import { languageTable } from './data';

const { Option } = Select;
function App() {
  const [isLoading, setLoading] = useState(false);
  const [isInputActive, setInputActive] = useState(false);
  const [isShowClearBtn, setClearBtnStatus] = useState(false);
  const [translateText, setTranslateText] = useState('');
  const [translateText1, setTranslateText1] = useState('');
  const [srcValue, setSrcValue] = useState('zh');
  const [targetValue, setTargetValue] = useState('en');
  const leftInputRef= useRef(null);
  const textareaRef = useRef(null);

  

  
  const onLoadingClick = () => {
    setLoading(true);
    const prompt = textareaRef.current.value.trim();
    console.log('prompt: ', prompt);
    if(!prompt) {
      setLoading(false);
      return;
    }

    const data = { 
      src: srcValue,
      tar: targetValue,
      prompt
    };
    
    fetch('/v1/engines/175b/completions', {
      method: 'POST', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
    .then(response => response.json())
    .then(data => {
      const responseText = data["choices"][0]["text"];
      const responseText1 = data["choices"][1]["text"];
      setTranslateText(responseText);
      setTranslateText1(responseText1);
      setLoading(false);
      
    })
    .catch((error) => {
      setLoading(false);
    });
  };



  const isParent =  (obj, parentObj) => {
    while (obj !== undefined && obj !== null && obj.tagName.toUpperCase() !== 'BODY'){
      if (obj === parentObj){
        return true;
      }
      obj = obj.parentNode;
    }
    return false;
  }

  // const onFullAreaClick = (e) => { 
  //   if(!isParent(e.target, leftInputRef.current)) {
  //     setInputActive(false);
  //   }
  // }

  const onMouseEnterInputArea = () => {
    setInputActive(true);
  }

  const onMouseLeaveInputArea = () => {
    const currentActiveElement = document.activeElement;
    if(currentActiveElement === textareaRef.current) {
      setInputActive(true);
    }else {
      setInputActive(false);
    }
    
  }

  const onSrcValueChange = (value) => {
    setSrcValue(value);
  }

  const onTargetValueChange = (value) => {
    setTargetValue(value);
  }

  const onInputChange = (e) => {
    const scrollHeight = textareaRef.current.scrollHeight;
    textareaRef.current.style.height = `${scrollHeight}px`;
    const currentValue = e.target.value.trim();
    setClearBtnStatus(currentValue ? true : false);
    // const inputValue = e.target.value;
    // setTranslateText(inputValue);
  }

  const onClearClick = () => {
    setClearBtnStatus(false);
    textareaRef.current.value = '';
    setTranslateText('');
    setTranslateText1('');
    textareaRef.current.focus();
    textareaRef.current.style.height = '162px';
  }

  const filterFun = (input, option) => {
    const chinese2pinyin = pinyin(option.value, { toneType: 'none', type: 'array' }).join('').toLowerCase();
    const abs2pinyin = pinyin(option.abs, { toneType: 'none', type: 'array' }).join('').toLowerCase();
    const en2pinyin = pinyin(option.en, { toneType: 'none', type: 'array' }).join('').toLowerCase();
    const inputPinyin = pinyin(input, { toneType: 'none', type: 'array' }).join('').toLowerCase();
    return  chinese2pinyin.includes(inputPinyin) || abs2pinyin.includes(inputPinyin) || en2pinyin.includes(inputPinyin) || option.pinyin.includes(inputPinyin);
  }



  //第二部分
  const [isLoading1, setLoading1] = useState(false);
  const [isInputActive1, setInputActive1] = useState(false);
  const [isShowClearBtn1, setClearBtnStatus1] = useState(false);
  const [translateTextTwo, setTranslateTextTwo] = useState('');
  const [translateText1Two, setTranslateText1Two] = useState('');
  const [srcValue1, setSrcValue1] = useState('zh');
  const [targetValue1, setTargetValue1] = useState('en');
  const leftInputRef1= useRef(null);
  const textareaRef1 = useRef(null);

  const defaultTextValue = '城市创新发展需要各行各业人才的支撑，而人才的发掘离不开“职业伯乐”。不久前，2022年度上海市人力资源服务“伯乐”奖励计划获奖名单出炉。这是上海人社部门为充分发挥市场在人力资源配置中的作用、激励和引导人力资源服务机构为本市用人单位选聘优秀人才而创设的奖项。三年来，44家次机构获得“伯乐”奖，累计奖励金额近3000万元。';

  const onLoadingClick1 = () => {
    setLoading1(true);
    const prompt = textareaRef1.current.value.trim();
    if(!prompt) {
      setLoading1(false);
      return;
    }

    const data = { 
      src: srcValue1,
      tar: targetValue1,
      prompt
    };
    
    fetch('/v2/engines/175b/completions', {
      method: 'POST', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
    .then(response => response.json())
    .then(data => {
      const responseText = data["choices"][0]["text"];
      const responseText1 = data["choices"][1]["text"];
      setTranslateTextTwo(responseText);
      setTranslateText1Two(responseText1);
      setLoading1(false);
      
    })
    .catch((error) => {
      setLoading1(false);
    });
  };

  

  // const isParent =  (obj, parentObj) => {
  //   while (obj !== undefined && obj !== null && obj.tagName.toUpperCase() !== 'BODY'){
  //     if (obj === parentObj){
  //       return true;
  //     }
  //     obj = obj.parentNode;
  //   }
  //   return false;
  // }

  const onFullAreaClick = (e) => { 
    if(!isParent(e.target, leftInputRef.current)) {
      setInputActive(false);
    }
    if(!isParent(e.target, leftInputRef1.current)) {
      setInputActive1(false);
    }
  }

  const onMouseEnterInputArea1 = () => {
    setInputActive1(true);
  }

  const onMouseLeaveInputArea1 = () => {
    const currentActiveElement = document.activeElement;
    console.log('currentActiveElement: ', currentActiveElement);
    if(currentActiveElement === textareaRef1.current) {
      setInputActive1(true);
    }else {
      setInputActive1(false);
    }
    
  }

  const onSrcValueChange1 = (value) => {
    setSrcValue1(value);
  }

  const onTargetValueChange1 = (value) => {
    setTargetValue1(value);
  }

  const onInputChange1 = (e) => {
    const scrollHeight = textareaRef1.current.scrollHeight;
    textareaRef1.current.style.height = `${scrollHeight}px`;
    const currentValue = e.target.value.trim();
    setClearBtnStatus1(currentValue ? true : false);
  }

  const onClearClick1 = () => {
    setClearBtnStatus1(false);
    textareaRef1.current.value = '';
    setTranslateTextTwo('');
    setTranslateText1Two('');
    textareaRef1.current.focus();
    textareaRef1.current.style.height = '162px';
  }

  // const filterFun = (input, option) => {
  //   const chinese2pinyin = pinyin(option.value, { toneType: 'none', type: 'array' }).join('').toLowerCase();
  //   const abs2pinyin = pinyin(option.abs, { toneType: 'none', type: 'array' }).join('').toLowerCase();
  //   const en2pinyin = pinyin(option.en, { toneType: 'none', type: 'array' }).join('').toLowerCase();
  //   const inputPinyin = pinyin(input, { toneType: 'none', type: 'array' }).join('').toLowerCase();
  //   return  chinese2pinyin.includes(inputPinyin) || abs2pinyin.includes(inputPinyin) || en2pinyin.includes(inputPinyin) || option.pinyin.includes(inputPinyin);
  // }

  return (
    <div className="App" onClick={onFullAreaClick}>
      <div className="App-main">
        <div className="App-header-wrap">
          <div className="App-header">
            <div className="App-header-import"></div>
            <div className="App-header-sub">致力于打造以中文核心的百语翻译。</div>
          </div>
          
        </div>
        <div className="App-main-inner">
          <div className="App-trans-operation-wrapper">
            <div className="App-left-selet">
              <Select
                showSearch
                style={{ width: 338 }}
                defaultValue={srcValue}
                placeholder="Search to Select"
                optionFilterProp="children"
                filterOption={(input, option) => filterFun(input, option)}
                onChange={(value)=> onSrcValueChange(value)}
              >
                {
                  languageTable.map((item,index)=>{
                    return <Option key={`${index}${item.en}`} value={item.abs} en={item.en} abs={item.abs} pinyin={item.pinyin}>{`${item.chinese}(${item.en})`}</Option>
                  })
                }
              </Select>
            </div>
            <div className="App-select-icon-wrap">
              <span className="App-select-icon"></span>
            </div>
            <div className="App-right-selet">
              <Select
                showSearch
                style={{ width: 342 }}
                defaultValue={targetValue}
                placeholder="Search to Select"
                optionFilterProp="children"
                filterOption={(input, option) => filterFun(input, option)}
                onChange={(value)=> onTargetValueChange(value)}
              >
                {
                  languageTable.map((item,index)=>{
                    return <Option key={`${index}${item.en}`} value={item.abs} en={item.en} abs={item.abs} pinyin={item.pinyin}>{`${item.chinese}(${item.en})`}</Option>
                  })
                }
              </Select>
            </div>

            <div className="App-submit">
              <Space style={{ width: '100%' }}>
                <Button type="primary" style={{width: "120px", background: "#F35D9D"}} loading={isLoading} onClick={() => onLoadingClick()}>
                  CONE翻译
                </Button>
              </Space>
            </div>
          </div>

          <div className="App-translateio">
            
            <div className="App-trans-left" ref={leftInputRef} onMouseEnter={onMouseEnterInputArea} onMouseLeave={onMouseLeaveInputArea}>
              <div className={`App-trans-input-wrap ${isInputActive ? 'App-trans-input-wrap-active': ''}`}>
                <div className="App-input-wrap"  dir="ltr">
                  <div className="App-textarea-wrap">
                    <div className="App-textarea-wrap-inner">
                      <textarea className="App-textarea"  ref={textareaRef} placeholder="请输入需要翻译的内容" onChange={(e)=>{onInputChange(e)}} defaultValue={defaultTextValue}></textarea>
                    </div>
                  </div>
                  <span className="App-clear-btn" style={{display: isShowClearBtn ? 'block' : 'none'}}>
                    <CloseOutlined onClick={onClearClick} style={{fontSize: 20, color: "#9C9C9C"}}/>
                  </span>
                </div>
              </div>
            </div>


          <Popover content="翻译文本" placement="bottom">
            <div className="App-trans-middle">
              <div className="App-trans-right-text-wrap">
                <p className="App-trans-right-text">{translateText}</p>
              </div>
            </div>
          </Popover>

            
            
          <Popover content="回译文本" placement="bottom">
          <div className="App-trans-right">
              <div className="App-trans-right-text-wrap">
                <p className="App-trans-right-text">{translateText1}</p>
              </div>
            </div>
          </Popover>

            

          </div>
        </div>

        
        {/* 第二部分 */}

        <div className="App-main-inner">
          <div className="App-trans-operation-wrapper">
            <div className="App-left-selet">
              <Select
                showSearch
                style={{ width: 338 }}
                defaultValue={srcValue1}
                placeholder="Search to Select"
                optionFilterProp="children"
                filterOption={(input, option) => filterFun(input, option)}
                onChange={(value)=> onSrcValueChange1(value)}
              >
                {
                  languageTable.map((item,index)=>{
                    return <Option key={`${index}${item.en}`} value={item.abs} en={item.en} abs={item.abs} pinyin={item.pinyin}>{`${item.chinese}(${item.en})`}</Option>
                  })
                }
              </Select>
            </div>
            <div className="App-select-icon-wrap">
              <span className="App-select-icon"></span>
            </div>
            <div className="App-right-selet">
              <Select
                showSearch
                style={{ width: 342 }}
                defaultValue={targetValue1}
                placeholder="Search to Select"
                optionFilterProp="children"
                filterOption={(input, option) => filterFun(input, option)}
                onChange={(value)=> onTargetValueChange1(value)}
              >
                {
                  languageTable.map((item,index)=>{
                    return <Option key={`${index}${item.en}`} value={item.abs} en={item.en} abs={item.abs} pinyin={item.pinyin}>{`${item.chinese}(${item.en})`}</Option>
                  })
                }
              </Select>
            </div>

            <div className="App-submit">
              <Space style={{ width: '100%' }}>
                <Button type="primary" style={{width: "120px", background: "#F35D9D"}} loading={isLoading1} onClick={() => onLoadingClick1()}>
                  NLLB翻译
                </Button>
              </Space>
            </div>
          </div>

          <div className="App-translateio">
            
            <div className="App-trans-left" ref={leftInputRef1} onMouseEnter={onMouseEnterInputArea1} onMouseLeave={onMouseLeaveInputArea1}>
              <div className={`App-trans-input-wrap ${isInputActive1 ? 'App-trans-input-wrap-active': ''}`}>
                <div className="App-input-wrap"  dir="ltr">
                  <div className="App-textarea-wrap">
                    <div className="App-textarea-wrap-inner">
                      <textarea className="App-textarea"  ref={textareaRef1} placeholder="请输入需要翻译的内容" onChange={(e)=>{onInputChange1(e)}} defaultValue={defaultTextValue}></textarea>
                    </div>
                  </div>
                  <span className="App-clear-btn" style={{display: isShowClearBtn1 ? 'block' : 'none'}}>
                    <CloseOutlined onClick={onClearClick1} style={{fontSize: 20, color: "#9C9C9C"}}/>
                  </span>
                </div>
              </div>
            </div>


          <Popover content="翻译文本" placement="bottom">
            <div className="App-trans-middle">
              <div className="App-trans-right-text-wrap">
                <p className="App-trans-right-text">{translateTextTwo}</p>
              </div>
            </div>
          </Popover>

            
            
          <Popover content="回译文本" placement="bottom">
          <div className="App-trans-right">
              <div className="App-trans-right-text-wrap">
                <p className="App-trans-right-text">{translateText1Two}</p>
              </div>
            </div>
          </Popover>

            

          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
